<template>
  <div class="container py-5">
    <!-- Pomodoro Container -->
    <div class="pomodoro-container mx-auto mb-4">
        <!-- Current Task -->
        <!-- Mode -->
        <div class="pomodoro-mode mx-auto">
          <button class="btn btn-outline-dark btn-lg" @click="pomofocus" :class="{active: this.mode === 'pomofocus'}">Pomofocus</button>
          <button class="btn btn-outline-dark ml-2 btn-lg" @click="shortBreak" :class="{active: this.mode === 'shortbreak'}">Break</button>
        </div>

        <!-- Session -->
        <div class="pomodoro-item timer">
          <div class="row-pomodoro">
            <div class="pickedTask">
              <h1 v-if="pickedTask" class="pomodoro-task">#{{pickedTask.name}}</h1>
            </div>
            <div id="show-timer">{{showTime | toMinSecond}}</div>
          </div>
        </div>

        <!-- Play/Pause/Reset -->
        <div class="pomodoro-item playerButton">
            <div class="row-pomodoro buttonRow">
                <div class="pomodoro-icon btn btn-outline-primary btn-lg" v-if="isStarted">
                  <span style="font-size: 40px" @click.stop.prevent="start">Start</span>
                </div>
                <div class="pomodoro-icon-set btn btn-block" v-else>
                  <span style="font-size: 40px" class="pomodoro-icon btn btn-outline-warning btn-lg" @click.stop.prevent="stopCountdown">Stop</span>
                  <span class="pomodoro-icon-reset btn btn-outline-danger" @click.stop.prevent="reset">Reset</span>
                </div>
            </div>
        </div>
    </div>
    <!-- <div class="inputTimer mt-3">
      <h3>Input Minites: 
        <input v-model="newTimer" @keyup.enter="inputTime" class="inputTime" id="inputTime" type="number" autofocus placeholder="minites...">
      </h3>
    </div> -->
    <Tasks 
      :tasks="tasks"
      @task-clicked="taskAfterClicked"
    />
    
  </div>
</template>

<script>
import Tasks from './../components/Tasks'
import axios from 'axios'
import sound from '../../audio/clock-alarm.mp3'

const audio = new Audio(sound)

export default {
  components: {
    Tasks,
  },
  data () {
    return {
      timer: null,
      showTime: -1,
      pomofocusTime: 1500,
      shortBreakTime: 300,
      tempTime: 0,
      isStarted: true,
      mode: 'pomofocus',
      tasks: [],
      pickedTask: []
    }
  },
  created () {
    this.fetchTasks()
    this.showTime = this.pomofocusTime
  },
  beforeDestroy () { // 轉跳Navbar時取消定時器
    this.reset()
  },
  methods: {
    async fetchTasks () {
      try {
        const { data } = await axios({
          method: 'post',
          url: '/',
          data: {
            token: localStorage.getItem('token') || '0'
          }
        })
        this.tasks = data.tasks
      } catch (error) {
        console.log('error', error)
      }
    },
    start() {
      if (this.showTime === 0) {
        return
      }
      this.timer = setInterval(this.countDown, 1000)
      this.isStarted = false
    },
    async countDown() {
      this.showTime--
      if (this.showTime === 0) {
        this.stopCountdown()
        audio.play()
        if (this.mode === 'pomofocus') {
          this.pickedTask.period += this.pomofocusTime
          this.pickedTask.counter += 1
          this.shortBreak()
          await axios({
            method: 'put',
            url: `tasks/${this.pickedTask._id}`,
            data: { period: this.pickedTask.period, counter: this.pickedTask.counter }
          })
          .then(response => console.log(response))
        } else {
          this.pomofocus()
        }
      }
    },
    stopCountdown() {
      clearInterval(this.timer)
      this.isStarted = true
    },
    reset() {
      clearInterval(this.timer)
      if (this.mode === 'pomofocus') {
        this.showTime = this.pomofocusTime
      }
      else if (this.mode === 'shortbreak') {
        this.showTime = this.shortBreakTime
      }
      this.isStarted = true
    },
    // 輸入的時間訂為"暫存時間"
    // inputTime() {
    //   this.tempTime = this.newTimer.trim() * 60

    //   if (this.mode === 'pomofocus') {
    //     this.pomofocusTime = this.tempTime
    //     this.showTime = this.pomofocusTime
    //   } 
    //   else if (this.mode === 'shortbreak') {
    //     this.shortBreakTime = this.tempTime
    //     this.showTime = this.shortBreakTime
    //   } 
    //   this.newTimer = ''
    // },

    // 模式切換
    pomofocus() {
      this.reset()
      this.showTime = this.pomofocusTime
      this.mode = 'pomofocus'
    },
    shortBreak() {
      this.reset()
      this.showTime = this.shortBreakTime
      this.mode = 'shortbreak'
    },
    taskAfterClicked(pickedTask) {
      this.pickedTask = pickedTask
      this.pomofocus()
    }
  },
  filters: {
    toMinSecond (time) {
      const minites = Math.floor(time / 60)
      const seconds = Math.floor(time % 60)
      if (minites < 10) {
        if (seconds < 10) {
          return `0${minites}:0${seconds}`
        }
        return `0${minites}:${seconds}`
      } else if (seconds < 10) {
        return `${minites}:0${seconds}`
      } else {
        return `${minites}:${seconds}`
      }
    }
  }
}
</script>

<style scoped>
  * {
    box-sizing: border-box;
  }
  body {
      margin: 0px;
      font-family: 'Arial', sans-serif;
      background-color: #181818;
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100vh;
  }
  .pomodoro-container {
      display: flex;
      flex-flow: row wrap;
      box-shadow: 0px 0px 1px 1px rgba(0,0,0,0.5);
      border: 1px solid black;
      border-radius: 1rem;
      padding: 20px;
      width: 600px;
  }
  .pomodoro-item {
      width: 100%;
  }
  .label {
      font-size: 0.7rem;
      text-transform: uppercase;
      color: #ffd800;
  }
  .row-pomodoro {
      margin: 8px 0 16px 0;
      text-align: center;
  }
  .timer {
      border-top: solid 3px #ffd800;
      margin-top: 12px;
  }
  .buttonRow {
      display: flex;
      justify-content: space-around;
  }
  .lengthTime {
      display: inline-block;
      padding: 0 20px 0 20px;
      font-size: 35px;
  }
  .btn-updown {
      font-size: 1.5rem;
      width: 30px;
      border: none;
      border-radius: 50%;
      cursor: pointer;
      color: #fff;
      background: transparent;
      box-shadow: 0px 0px 10px 10px rgba(0,0,0,0.3);
  }
  .btn-controller {
      border: none;
      cursor: pointer;
      font-size: 1.5rem;
      background-color: transparent;
      color: #fff;
  }
  /* Pomodoro item row */
  #show-timer {
      font-size: 5rem;
      font-weight: 500;
  }
  .pomodoro-task {
      font-size: 3rem;
      font-weight: 700;
  }
  .btn-lg {
    width: 250px
  }
  .pomodoro-icon-set {
    position: relative;
  }
  .pomodoro-icon-reset {
    position: absolute;
    top: 25%;
    right: 10%;
  }
</style>