<template>
  <div class="tasks">
    <form action="" class="form-inline mb-3" @submit.prevent.stop="handleSubmit">
      <div class="form-row">
        <label for="newTask" class="col-2">Task: </label>
        <input v-model="newTask" name="newTask" id="newTask" type="text" class="form-control col-7 mr-2" placeholder="input a new task..." required>
        <input v-model="predictedTime" name="predictedTime" id="predictedTime" type="number" class="form-control col-2" required>
      </div>
      <button type="submit" class="btn btn-primary ml-2">Submit</button>
    </form>
    <table class="table table-hover">
      <thead class="thead-light">
        <tr>
          <th scope="col" width="30">#</th>
          <th scope="col">Task</th>
          <th scope="col" width="60">Numbers</th>
          <th scope="col" width="90">Status</th>
        </tr>
      </thead>
      <tbody>
        <tr class="task-list" v-for="(task, index) in tasks" :key="task._id" @click.stop.prevent="pickTask(task)">
          <th>{{ index + 1 }}</th>
          <td>{{task.name}}</td>
          <td>{{task.counter}}/{{task.predictedTime}}</td>
          <td v-if="task.counter >= task.predictedTime">Completed</td>
          <td v-else>Incompleted</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import {v4 as uuidv4} from 'uuid'
import axios from 'axios'

export default {
  props: {
    tasks: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      task: [],
      newTask: '',
      predictedTime: 1,
    }
  },
  methods: {
    async handleSubmit() {
      try { 
        this.predictedTime = this.predictedTime.toString().trim()
        this.task.push({ id: uuidv4(), name: this.newTask, predictedTime: Number(this.predictedTime) })
        axios({
          method: 'post',
          url: `tasks`,
          data: {
            name: this.task[0].name,
            predictedTime: this.task[0].predictedTime,
            token: localStorage.getItem('token') || '0'
          }
        })
        .then( (response) => console.log(response))
        this.tasks.push({...this.task[0], counter: 0})
        this.task = []
        this.newTask = ''
      } catch (error) {
        console.log('error', error)
      }
    },
    async pickTask (task) {
      const pickedTask = task
      this.$emit('task-clicked', pickedTask)
    }
  }
}
</script>

<style scoped>
  .table {
    cursor: pointer;
  }
</style>