<template>
<div class="container-fuild">
  <nav class="navbar bg-light" v-if="isAuthenticated">
    <ul class="nav nav-pills justify-content-end ml-auto mr-3">
      <li
        v-for="tab in tabs"
        :key="tab.id"
        class="nav-item"
      >
        <router-link
          :to="tab.path"
          class="nav-link"
          @click.prevent.stop="clearInterval"
        >
        {{ tab.title }}
        </router-link>
      </li>
    </ul>
    <template class="navbar-nav">
        <h3 class="mr-3">
          {{ currentUser.name || '使用者' }} 您好  
        </h3>
        <button
          type="button"
          class="btn btn-sm btn-outline-warning my-2 my-sm-0"
          @click="logout"
        >
          登出
        </button>
    </template>
  </nav>
  <nav class="navbar bg-light" v-else>
    <ul class="nav nav-pills justify-content-end ml-auto mr-3">
      <li
        v-for="tab in activeFrom(tabs, 1)"
        :key="tab.id"
        class="nav-item"
      >
        <router-link
          :to="tab.path"
          class="nav-link"
        >
        {{ tab.title }}
        </router-link>
      </li>
      <li
        v-for="tab in disabledFrom(tabs, 1)"
        :key="tab.id"
        class="nav-item"
        @click.prevent.stop="showAlert"
      >
        <router-link
          :to="tab.path"
          class="nav-link disabled"
        >
        {{ tab.title }}
        </router-link>
      </li>
    </ul>
    <template class="navbar-nav">
      <h3 class="mr-3">
        訪客 您好
      </h3>
      <router-link
        type="button"
        class="btn btn-sm btn-outline-success my-2 my-sm-0"
        :to="'/signin'"
      >
          登入
      </router-link>
    </template>
  </nav>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Toast } from '../utils/helpers'
import {v4 as uuidv4} from 'uuid'

export default {
  data () {
    return {
      timer: null,
      tabs: [
        {
          id: uuidv4(),
          title: 'Pomofocus',
          path: '/'
        },
        {
          id: uuidv4(),
          title: 'Report',
          path: '/report'
        },
        {
          id: uuidv4(),
          title: 'Statistic',
          path: '/statistic'
        }
      ]
    }
  },
  computed: {
    ...mapState([ 'currentUser', 'isAuthenticated' ])
  },
  methods: {
    logout () {
      this.$store.commit('revokeAuthentication')
      this.$router.push('/signin')
      Toast.fire({ icon: 'success', title: '成功登出!' })
    },
    disabledFrom (arr, idx) {
      return arr.slice(idx)
    },
    activeFrom (arr, idx) {
      return arr.slice(0, idx)
    },
    showAlert() {
      Toast.fire({ icon: 'warning', title: '此功能只有登入後才可使用!' })
    }
  }
}
</script>